"use client";

import { useState } from "react";
import Link from "next/link";
import { FaHome, FaChartLine, FaDollarSign, FaUniversity, FaUser, FaSignInAlt } from "react-icons/fa";

export default function Dashboard() {
  const [activeTab, setActiveTab] = useState("home");
  const [isLoggedIn, setIsLoggedIn] = useState(false); // ตรวจสอบสถานะการล็อกอิน

  return (
    <div className="flex h-screen bg-gray-900 text-white">
      {/* Sidebar */}
      <aside className="w-64 bg-gray-800 p-6 flex flex-col">
        {/* Logo */}
        <div className="flex items-center space-x-2 mb-8">
          <div className="bg-green-500 p-2 rounded-lg">
            <span className="text-black font-bold text-xl">G</span>
          </div>
          <h1 className="text-2xl font-bold">EQUITY</h1>
        </div>

        {/* Menu */}
        <nav className="flex flex-col space-y-4">
          <button
            className={`flex items-center space-x-2 p-3 rounded-lg ${activeTab === "home" ? "bg-gray-700" : ""}`}
            onClick={() => setActiveTab("home")}
          >
            <FaHome />
            <span>Home</span>
          </button>
          <button className="flex items-center space-x-2 p-3 rounded-lg">
            <FaChartLine />
            <span>Chart</span>
          </button>
          <button className="flex items-center space-x-2 p-3 rounded-lg">
            <FaDollarSign />
            <span>Asset</span>
          </button>
          <button className="flex items-center space-x-2 p-3 rounded-lg">
            <FaUniversity />
            <span>Finance</span>
          </button>
        </nav>

        {/* Login Button or User Profile */}
        <div className="mt-auto">
          {isLoggedIn ? (
            <div className="flex items-center space-x-2 p-3 rounded-lg bg-gray-700">
              <FaUser />
              <span>Arin</span>
            </div>
          ) : (
            <Link href="/login">
              <button className="w-full flex items-center justify-center space-x-2 p-3 rounded-lg bg-blue-600 text-white font-semibold shadow-lg transition hover:bg-blue-700">
                <FaSignInAlt />
                <span>Login</span>
              </button>
            </Link>
          )}
        </div>
      </aside>

      {/* Main Content */}
      <main className="flex-1 p-8 bg-gray-900">
        <h2 className="text-xl font-bold mb-4">ASSETS</h2>

        {/* Asset Cards */}
        <div className="grid grid-cols-3 gap-6">
          <div className="bg-gradient-to-r from-purple-500 to-indigo-500 p-6 rounded-lg">
            <h3 className="text-lg">Bitcoin</h3>
            <p className="text-3xl font-bold">$1,820</p>
            <p className="text-green-300">+1.84%</p>
          </div>
          <div className="bg-gradient-to-r from-indigo-500 to-blue-500 p-6 rounded-lg">
            <h3 className="text-lg">Ethereum</h3>
            <p className="text-3xl font-bold">$1,100</p>
            <p className="text-green-300">+2.87%</p>
          </div>
          <div className="bg-gray-800 p-6 rounded-lg flex justify-center items-center text-gray-400">
            <span>+ New Asset</span>
          </div>
        </div>

        {/* Activity Table */}
        <h2 className="text-xl font-bold mt-8 mb-4">ACTIVITY</h2>
        <div className="bg-gray-800 p-6 rounded-lg">
          <table className="w-full">
            <thead>
              <tr className="text-left border-b border-gray-700">
                <th className="pb-2">Transactions</th>
                <th className="pb-2">Amount</th>
                <th className="pb-2">Total</th>
                <th className="pb-2">Status</th>
                <th className="pb-2">Date</th>
              </tr>
            </thead>
            <tbody>
              <tr className="border-b border-gray-700">
                <td className="py-2">Ethereum Purchased</td>
                <td>0.056 ETH</td>
                <td>$100.00</td>
                <td className="text-yellow-400">Pending</td>
                <td>Feb 14, 2023</td>
              </tr>
              <tr className="border-b border-gray-700">
                <td className="py-2">Bitcoin Purchased</td>
                <td>0.3 BTC</td>
                <td>$100.00</td>
                <td className="text-green-400">Done</td>
                <td>Feb 14, 2023</td>
              </tr>
              <tr>
                <td className="py-2">Bitcoin Purchased</td>
                <td>0.025 BTC</td>
                <td>$100.00</td>
                <td className="text-green-400">Done</td>
                <td>Jan 14, 2023</td>
              </tr>
            </tbody>
          </table>
        </div>

        {/* Bitcoin Price */}
        <div className="mt-8 bg-gray-800 p-6 rounded-lg">
          <h3 className="text-lg">BITCOIN</h3>
          <p>Updated: <span className="text-gray-400">5 seconds ago</span></p>
          <p>Change: <span className="text-red-400">-2.64%</span></p>
          <p>Price: <span className="text-white">$12,728</span></p>
        </div>
      </main>
    </div>
  );
}
